import React, {Component} from 'react';
import Slider from 'react-slick';

 class TestimonialSlider extends Component {
    render() {
      const settings = {
        dots: true,
        infinite: true,
        speed: 1800,
        arrows: false,
        autoplay: true,
        pauseOnHover:true,
        autoplaySpeed: 10000,
        slidesToShow: 1,
        slidesToScroll: 1
      };
      return (
        <div>
          <Slider {...settings} className="testimonial_slider">
                <div className="item">
                    <div className="author_img">
                        <img src={require('../../image/slider2.png')} alt=""/>
                    </div>
                    <h6>Video inspekcija kanalizacije</h6>
                    <span>Dodatne usluge</span>
                    <p>Tehnologija snimanja kanalizacije ima za svrhu da precizno locira uzrok zaepljenja, otecenost ili devijaciju instalacije.
					Ova tehnologija u mnogome olakava detektovanje problema a samim tim i njegovo reavanje.
					Kolorni monitoring, prenik glave kamere 8.50mm, snimanje snimka ili fotografije.
					</p>
                </div>
                <div className="item">
                    <div className="author_img">
                        <img src={require('../../image/our-coaches-2.jpg')} alt=""/>
                    </div>
                    <h6>Buenje betona</h6>
                    <span>Dodatne usluge</span>
                    <p>Pod buenjem betona se podrazumeva buenje bez opiljaka delova betona, to za rezultat ima perfektnu i ravnu rupu.
					Izrada krunih otvora vertikalno, horizontalno ili koso buenje za montau raznih vrsta instalacija
					sada je vrlo jednostavno, brzo i kvalitetno ak i ako se radi o armirano betonskom materijalu.</p>
                </div>
                <div className="item">
                    <div className="author_img">
                        <img src={require('../../image/our-coaches-7.jpg')} alt=""/>
                    </div>
                    <h6>Odravanje instalacija</h6>
                    <span>Dodatne usluge</span>
                    <p>Tekue odravanje instalacija je izvoenje radova koji se preduzimaju radi spreavanja oteenja koja nastaju upotrebom ili
					eksploatacijom. Radovi se sastoje od pregleda, popravki i preduzimanja preventivnih i zatitnih mera, odnosno svih radova 
					kojima se obezbeuje odravanje instalacija na zadovolavajuem nivou upotreblivosti.</p>
                </div>
          </Slider>
        </div>
      );
    }
  }
  export default TestimonialSlider;